import React from 'react';
import '../../App.css';
import './Middle.css';
import video from '../../tempfiles/yone-fanart-login-screen-animation-loop-league-of-legends.mp4'
import axios from 'axios';


class Middle extends React.Component{

  componentDidMount() {
    axios.get(`https://euw1.api.riotgames.com/lol/summoner/v4/summoners/by-name/Lamia%20Classy?api_key=RGAPI-bb8d8c06-a5ce-45ae-ac17-3f10eaa225e8`)
      .then(res => {
        console.log(res)
      })
  }

  render(){
    return (
      <div className='mid-container'>
        <video src={video} autoPlay loop muted/>
        <div className="search">
        <select id='servers' className="servers">
            <option value="EUW">EUW</option>
            <option value="NA">NA</option>
            <option value="EUNA">EUNA</option>
            <option value="TU">TU</option>
            <option value="JP">JP</option>
            <option value="OC">OC</option>
        </select>

        <input
            type="search"
            id="summoner"
            className="input"
            aria-label="Search through site content"
            placeholder='Ton pseudo'
           />

        <button onClick={() => alert("trop bien django")} className="Research">Rechercher</button>
        </div>
    </div>
    );
  }
}

export default Middle;
